import React, { useEffect, useState } from 'react';
import {
  Button,
  Modal,
  Box,
  Switch,
  TextField,
  FormControl,
  FormGroup,
  Select,
  MenuItem,
} from '@mui/material';
import moment from 'moment';
import { Card } from '../components/Card';
import { getPlayerApi, updatePlayerApi } from '../api';
import { DEFAULT_DESK } from '../constant';
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};
export const Settings = (props) => {
  const { setGameInfo, setActions, gameInfo, logs } = props;
  const resetGame = () => {
    setGameInfo(null);
    setActions([]);
    setPlayers([]);
  };
  const [players, setPlayers] = useState([]);
  const [editModal, setEditMoal] = useState(false);
  const [info, setInfo] = useState({});

  const copyText = () => {
    let text = '';
    text = text + 'Report game ' + logs.info.id + '\n';
    text = text + 'Phe chin thng: ' + logs.info.winner + '\n';
    const time = moment(logs.info.created_at);
    text = text + 'Bt u: ' + time.format('MMM D, h:mm') + '\n';
    logs.players.forEach((player) => {
      text = text + player.name + ': ' + player.role;
      text = text + '\n';
    });
    text = text + logs.actions.map((action) => {
      return action.join('\n');
    }).join('\n');
    copyToClipboard('```'+text+'```');
  };


  const copyToClipboard = (content) => {
    if (window.isSecureContext && navigator.clipboard) {
      navigator.clipboard.writeText(content);
    } else {
      unsecuredCopyToClipboard(content);
    }
  };

  const unsecuredCopyToClipboard = (text) => {
    const textArea = document.createElement('textarea');
    textArea.value=text; document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();
    try {
      document.execCommand('copy');
    } catch (err) {
      console.error('Unable to copy to clipboard');
    }
    document.body.removeChild(textArea);
  };

  useEffect(() => {
    if (gameInfo !== null) {
      getPlayerApi(gameInfo.id, (response) => {
        setPlayers(response.data.players);
      });
    }
  }, []);

  return (
    <>
      <Button
        variant='outlined'
        onClick={resetGame}
      >
        Reset
      </Button>
      <Button
        variant="outlined"
        onClick={copyText}
      >
        Copy
      </Button>
      <hr/>
      { players.map((player) =>
        <Card
          key={player.id}
          icon={player.role}
          text={player.name}
          lover={player.lover}
          onClick={() => {
            setEditMoal(true);
            setInfo(player);
          }}
        />,
      )}
      <Modal
        open={editModal}
        onClose={() => setEditMoal(false)}
      >
        <Box sx={{ ...style, width: 300 }}>
          <FormControl>
            <FormGroup>
              Status
              <Switch
                checked={info.status}
                onClick={() => {
                  setInfo({ ...info, status: !info.status });
                }}
              />
            </FormGroup>
            <FormGroup>
              <TextField
                label="Name"
                variant="outlined"
                onChange={(e) => setInfo({ ...info, name: e.target.value })}
                value={info.name}
              />
            </FormGroup>
            <FormGroup>
              <Select
                onChange={(e) => {
                  setInfo({ ...info, role: e.target.value });
                }}
                value={info.role}
              >
                {DEFAULT_DESK.map((role) =>
                  <MenuItem value={role.key} key={role.key}>{role.name}</MenuItem>,
                )}


              </Select>
            </FormGroup>
            <Button
              variant='outlined'
              onClick={() => {
                updatePlayerApi(gameInfo.id, info);
                setEditMoal(false);
                getPlayerApi(gameInfo.id, (response) => {
                  setPlayers(response.data.players);
                });
              }}
            >
              Save
            </Button>
          </FormControl>
        </Box>
      </Modal>

    </>
  );
};
