import { URL } from '../shared/api';
import axios from 'axios';

export const newGameApi = async (allRoles, players, grantMode, callback) => {
  const roles = allRoles.filter((r) => r.number > 0);
  const token = sessionStorage.getItem('token');
  const params = {
    roles,
    players,
    token,
    grant_mode: grantMode,
  };
  const api = URL + 'games';
  const response = await axios.post(api, params);
  callback(response.data);
};

export const newActionApi = async (actions, gameId, callback) => {
  const params = {
    actions,
  };
  const api = URL + 'games/' + gameId + '/actions';
  const response = await axios.post(api, params);
  callback(response.data);
};

export const hunterApi = async (id, callback) => {
  const params = {
    id,
  };
  const api = URL + 'games/0/hunt';
  const response = await axios.post(api, params);
  callback(response.data);
};

export const getProcessApi = async (id, callback) => {
  const api = URL + 'games/'+ id +'/game_process';
  const response = await axios.post(api);
  callback(response.data);
};

export const collectInfoApi = async (id, callback) => {
  const api = URL + 'games/'+ id +'/collect_info';
  const response = await axios.get(api);
  callback(response.data);
};

export const getPlayerApi = async (gameId, callback) => {
  const api = URL + 'games/' + gameId + '/players';
  const response = await axios.get(api);
  callback(response.data);
};

export const getTopPlayerApi = async (callback) => {
  const api = URL + 'players/top';
  const response = await axios.get(api);
  callback(response.data);
};

export const updatePlayerApi = async (gameId, info) => {
  const api = URL + 'games/' + gameId + '/players/' + info.id;
  await axios.put(api, info);
};
